.not-found {
	height: 100vh;
	position: relative;

	&::before {
		background-color: $black;
		content: "";
		display: block;
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		opacity: 0.75;
	}

	// not-found__wrapper
	&__wrapper {
		position: relative;
		text-align: center;
		top: 50%;
		transform: translateY(-50%);
		z-index: 100;
	}

	// not-found__svg
	&__svg {
		max-width: 540px;
	}

	// not-found__title
	&__title {
		color: $white;
		font-size: 24px;
		font-family: $kaleko-bold;
		margin: 20px 0;
	}

	// not-found__footer
	&__footer {
		color: $white;
		font-size: 14px;
		padding: 0 20px;
		width: 100%;
		position: absolute;
		bottom: 20px;
		left: 0;
		text-align: center;
		z-index: 100;

		a {
			color: $white;
		}
	}
}

@media screen and (min-width: 768px) {
	.not-found {
		// not-found__title
		&__title {
			font-size: 36px;
			margin: 60px 0;
		}

		// not-found__footer
		&__footer {
			bottom: 40px;
		}
	}
}